.Asset {
    text-align: center;
    color: black;
    background-color: #fefefe;
    background-image: linear-gradient(315deg, #fefefe 0%, #00a4e4 74%);
    min-height: 100vh;
}
.Asset-header{
    min-height: 10vh;
    display: flex;
    flex-direction: row;
    gap: .25em;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
}
.charts-assets{
    display:flex;
    flex-flow: column;
}
.asset-list{
    list-style: none;
    display: flex;
    flex-flow: row;
    gap: 1em;
    padding: 0;
    margin: 0 0 1em 1em;
}
.asset-list>li:hover{
    text-decoration: underline;
    border-radius: .2em;
    opacity: 0.65;
}
.asset-list>li>a:visited{
    color: white;
}
.asset-list>li>a:link{
    text-decoration: none;
    color: ghostwhite;
    opacity: .75;
}
.asset-list>li>a:active{
    text-decoration: none;
    background-color: blue;
    color: ghostwhite;
    opacity: 1;
}
.spinner{
    display: flex;
    justify-content: center;
    margin: 5em auto;
}

.highcharts-container {
    border-radius: 1em;
    border: 1px solid black;
}
.averages{
    display: flex;
    justify-content: center;
    margin: 1em 0;
    gap: 0.5em;
    flex-flow: column;
}
